import PageButton from '../../components/button.vue';
import EmptyView from '../../components/emptyview.vue';

export default {
	components: {
		PageButton,
		EmptyView
	},
	data() {
		return {
			learnList: [],
			page: 1,
			size: 10,
			totalPages: 1
		}
	},
	onShow() {
		let that = this;
		that.learnList = [];
		that.page = 1;
		that.size = 10;
		that.totalPages = 1;
		that.getlearnList();
	},
	onLoad() {

	},
	onPullDownRefresh() {
		// 增加下拉刷新数据的功能
		var self = this;
		self.getlearnList();
	},
	methods: {
		getlearnList: function() {
			let that = this;
			if (that.totalPages <= (that.page - 1) * that.size) {
				return;
			}
			var param = {};
			param.pageNum = that.page;
			param.pageSize = that.size;
			that.$util.request({
				url: "/didi-doctor/ossfile/findList",
				param: param,
				contentType: 'application/x-www-form-urlencoded',
				success: function(res) {
					var data = res.data;
					if (data == null) {
						return;
					}
					that.learnList = that.learnList.concat(data);
					that.page = data.pageNum + 1;
					that.totalPages = data.total;
				},
				error: function() {}
			})
		},
		downloadlearn:function(url){
			uni.downloadFile({
			    url: url, 
			    success: function(res) {
					uni.playVoice({
					        filePath: res.tempFilePath
					      })
			    },
				fail:function(res) {
					console.log(res);
				}
			});
		}
	}
}
